import {Nav, Navbar,NavLink} from 'react-bootstrap';
import { render } from 'react-dom';
import React from 'react';
import './index.css';

class NavBar extends React.Component {

  navExpanded = React.createRef();
  navTitle = React.createRef();

  componentDidMount(){
    this.navExpanded.current.style.display = 'none';
  }

  showNavExpanded(){
    this.navExpanded.current.style.display = 'inline';
  }

  hideNavExpanded(){
    this.navExpanded.current.style.display = 'none';
  }

render(){
    return(
        <Navbar className="nvbar" expand="lg">
          <div
            ref = {this.navTitle}
            className="nvbar-content"
            onMouseEnter={()=>{this.showNavExpanded()}}
            onMouseLeave={()=>{this.hideNavExpanded()}}
            >
          <span> &lt; </span>

          <div className="nv-expanded" ref={this.navExpanded}>
            <Nav.Link><span className="nv-expanded-contents">Development</span></Nav.Link>
            <Nav.Link><span className="nv-expanded-contents">Football</span></Nav.Link>
            <Nav.Link><span className="nv-expanded-contents">Stories</span></Nav.Link>
          </div>
          <span> /&gt;</span>
          </div>
        </Navbar>

    )
}
}

export default NavBar;
